import {
    Component,
    EventEmitter,
    HostBinding,
    Output
} from '@angular/core';
/**
 * @hidden
 */
@Component({
  selector: 'kendo-dialog-titlebar',
  template: `
    <div class="k-window-title">
      <ng-content></ng-content>
    </div>
    <div class="k-window-actions">
      <span class="k-window-action k-button" role="button" (click)="onClose.emit()" aria-label="Close">
        <span class="k-icon k-i-x"></span>
      </span>
    </div>
  `
})
export class DialogTitleBarComponent {
  @Output() public onClose: EventEmitter<any> = new EventEmitter();

  @HostBinding('class') get className(): string {
    return 'k-window-titlebar k-header';
  }
}




